@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: cursive, sans-serif !important;

  /* background-color: #a871fc !important; */
}

::-webkit-scrollbar {
  width: 5px;
  height: 2px;
}

::-webkit-scrollbar-thumb {
  background: #7550a6;
  border-radius: 0px;
  box-shadow: inset 2px 2px 2px hsla(0, 0%, 100%, 0.25),
    inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-track {
  background: rgb(149, 152, 255);
}

@-webkit-keyframes float {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  60% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

@keyframes float {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  60% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

.db-search {
  display: flex;
  list-style: none;
  padding-right: 2rem;
}

.db-search li {
  text-decoration: none;
  background: none;
}

.db-search img:hover {
  cursor: pointer;
}

.video {
  position: relative;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 800px;
  height: auto;
}

.warning {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: white;
    padding: 1rem;
    text-align: center;
    width: 30rem;
    z-index: 10;
    position: fixed;
    top: 20vh;
    left: calc(50% - 15rem);
  }
  
  .backdrop {
    position: fixed;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.75);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }


  .btn {
    font: inherit;
    padding: 0.5rem 1.5rem;
    cursor: pointer;
    border-radius: 4px;
    background-color: #800040;
    color: white;
    border: 1px solid #800040;
    margin: 0 1rem;
  }
  
  .btn:hover {
    background-color: #9c1458;
    border-color: #9c1458;
  }
  
  .btn--alt {
    background-color: transparent;
    color: #800040;
  }
  
  .btn--alt:hover {
    background-color: #f8dae9;
  }
.navs {
  position: fixed;
  width: 100%;
